
import { defineComponent, ref } from "vue";

export default defineComponent({
  name: "Header",
  props: {
    addTodos: {
      type: Function,
      required: true,
    },
  },
  setup(props) {
    const text = ref("");
    const addTodo = () => {
      if(text.value.trim()){
        props.addTodos({ id: Date.now(), title: text.value, isCompleted: false });
        text.value= ""
      }
    };
    return {
      addTodo,
      text,
    };
  },
});
